import React from "react";
import { CustomColumns, Tag as TagType } from "../../types";
import { useQuery } from "react-query3";
import { Space, Tag } from "antd";
import { Modal } from "components/ui/Modal";

const Table = React.lazy(() => import("components/ui/Table"));
const EditModal = React.lazy(() => import("components/ui/EditModal"));
const DeleteButton = React.lazy(() => import("components/ui/DeleteButton"));
const NewTagModal = React.lazy(() => import("./tagModal"));

export default function Tags() {
  const { data: tags, isLoading } = useQuery<TagType[]>("/tag");

  const columns: CustomColumns<TagType>[] = [
    {
      key: "name",
      title: "Name",
      dataIndex: "name",
      width: "fit-content",
      editable: true,
      render: (text, record) => {
        return <Tag color={record.color}>{record.name}</Tag>;
      },
    },
    {
      align: "left",
      key: "description",
      title: "Description",
      dataIndex: "description",
      editable: true,
      colSpan: 2,
    },
    {
      key: "color",
      title: "Color",
      editable: true,
      colSpan: 0,
      rowSpan: 0,
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      editable: false,
      render: (text, record) => (
        <Space>
          <Modal>
            <EditModal record={record} allowedWithOneWayGitSync={false} />
          </Modal>
          <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
        </Space>
      ),
    },
  ];

  return (
    <Table
      title="Tags"
      actions={[
        <Modal>
          <NewTagModal />
        </Modal>,
      ]}
      columns={columns}
      data={tags}
      loading={isLoading}
      showHeader={false}
    />
  );
}
